import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {UserReferralComponent} from './component/user-referral/user-referral.component';
import {RegistrationPageComponent} from "./component/registration-page/registration-page.component";
import {PromoCodeResolver} from "./resolvers/promo-code-resolver.resolver";
import {NotFound404ComponentComponent} from "./not-found404-component/not-found404-component.component";

const routes: Routes = [
  {path: 'referral-page', component: UserReferralComponent},
  {path: 'registration-page', component: RegistrationPageComponent},
  {
    path: 'registration-page/:promo-code',
    component: RegistrationPageComponent,
    resolve: {promoCode: PromoCodeResolver}
  },
  {path: '', redirectTo: '/referral-page', pathMatch: 'full'},
  {path: '**', component: NotFound404ComponentComponent},
  {path: '**', redirectTo: '/404'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
