import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ClickToChatService} from "../../services/click-to-chat.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-user-referral',
  templateUrl: './user-referral.component.html',
  styleUrls: ['./user-referral.component.css']
})
export class UserReferralComponent implements OnInit {
  promoCodeForm: FormGroup;
  url = '';
  mobNumPattern = /^\d{4}$/;

  constructor(private fb: FormBuilder, private router: Router, private clickToChat: ClickToChatService) {
  }

  ngOnInit() {
    this.initializeContactForm();
  }

  initializeContactForm() {
    this.promoCodeForm = this.fb.group({
      promoCode: ['', [Validators.required, Validators.pattern(this.mobNumPattern)]]
    });
  }


  submitPromoCode() {
    const promocode = this.promoCodeForm.value.promoCode;
    const urlText = 'signup on bettybingo at http://bettybingo.co.zm/registration-page/' + promocode + ' for amazing gaming experience';
    console.log('urltext', urlText);
    const encodedUrlText = encodeURIComponent(urlText);
    // const encodedUrlText = encodeURI(urlText);
    console.log('encodedurltext', encodedUrlText);
    const finalUrl = `https://wa.me/?text=${encodedUrlText}`;
    console.log('final url', finalUrl);
    window.location.href = finalUrl;

  }
}
